.App {
  text-align: center;
  max-width: 500px;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: auto;
  border-radius: 12.5px;
  margin-top: 2vh;
  background-color: whitesmoke;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Button {
  font-size: larger;
  font-weight: bolder;
  background-color: rgb(255, 177, 60);
  color: white;
  width: 230px;
  height: 55px;
  border-radius: 12.5px;
  border: solid rgb(255, 100, 60) 2px;
  margin: 0.2vw;
}

.NextRound{
  width: 55px;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.PlotWrapper{
  height: 30vh;
  padding: 5vw;
  font-family: 'Times New Roman', Times, serif;
  font-size: larger;
}

.Input{
    border: none;
    border-bottom: black solid;
    font-size: larger;
    letter-spacing: 3px;
    text-align: center;
    margin-bottom: 10px;
    margin-top: 10px;
    height: 25px;
}

.Results{
  display: flex;
  align-items: center;
  font-size: x-large;
  height: 24px;
  margin-bottom: 4px;
}

.Suggested {
  position: absolute;
  top: auto;
  bottom: 100%;
  color: #fff;
  background: #000;
  width: 100%;
}

.SuggestedTitle {
  padding: 1rem;
}
.Separator{
  border: rgb(255, 177, 60) solid 1px;
}
.InputWrap{
  position: relative;
  text-align: center;
  width: 89%;

}

.KeyBoard{
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.Key{
  border: rgb(255, 177, 60) solid 2px;
  width: 25px;
  height: 35px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.Key:active{
  background-color: rgb(255, 177, 60);
}

body{
  background-color: wheat;
  user-select: none;
}
